.record_form {
	margin-bottom: rem(310);
	position: relative;

	.container {
		position: relative;
	}

	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		top: rem(-157);
		left: 0;
		width: rem(910);
		height: rem(1154);
		background: url(../img/section9/left_bg.png) no-repeat;
		background-size: 100%;
	}

	&__right {
		position: absolute;
		top: rem(-70);
		right: rem(-200);
		z-index: -1;

		height: rem(810);

		img {
			width: 100%;
			height: rem(840);
			display: block;
			object-fit: cover;
		}
	}
}