.info_company {

	margin-bottom: rem(100);

	> .container {
		display: flex;
		flex-direction: column;
	}

	&__top {
		display: flex;
		flex-wrap: wrap;

		margin-bottom: rem(150);

		&_left {
			margin-right: rem(75);
			flex: 0 0 rem(223);
			max-width: rem(223);
		}

		&_right {
			display: flex;
			flex-direction: column;

			flex: 1;

			.sect-title-40 {
				margin-bottom: rem(35);
			}

			.sect-text {
				margin-bottom: rem(28);

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;

		.item {
			display: flex;
			flex-direction: column;
			align-items: center;

			&__image {
				width: rem(75);
				height: rem(75);
				border-radius: 50%;
				background-color: #E2DBD0;

				position: relative;

				margin-bottom: rem(20);

				svg {
					position: absolute;
					top: 0;
					right: 0;
				}
			}

			&__text {
				text-align: center;
				font-size: rem(18);
				line-height: 130%;
				color: var(--c-text-black);
			}

			&:first-child {
				flex: 0 0 rem(210);
				max-width: rem(210);

				margin-right: rem(44);

				.item__image {
					svg {
						top: rem(14);
						right: rem(4);
					}
				}
			}

			&:nth-child(2) {
				flex: 0 0 rem(303);
				max-width: rem(303);

				margin-right: rem(14);

				.item__image {
					svg {
						top: rem(12);
					}
				}
			}

			&:nth-child(3) {
				flex: 0 0 rem(269);
				max-width: rem(269);

				margin-right: rem(77);

				.item__image {
					svg {
						top: rem(12);
					}
				}
			}

			&:last-child {
				margin-right: 0;
				flex: 0 0 rem(176);
				max-width: rem(176);

				.item__image {
					svg {
						top: rem(12);
					}
				}
			}
		}
	}

}