.modal_overlay {
	position: fixed;

	z-index: 100;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	// display: flex;
	align-items: center;
	justify-content: center;

	display: none;

	.modal_wrap {
		position: relative;
		width: rem(590);
		background-color: white;

		padding: rem(110);

		@include min1440 {
			padding: rem(70);
		}

		&__close {
			position: absolute;
			top: rem(40);
			right: rem(40);
		}

		.sect-title-28 {
			margin-bottom: rem(24);
		}

		.sect-text {
			margin-bottom: rem(60);
		}
		
		.modal_form {
			display: flex;
			flex-direction: column;

			.field-set {
				margin-bottom: rem(30);

				.icon {

				}

				.field {
					border-color: rgba(40, 40, 46, .2);
					color: #7D7D80;
				}

				&:last-child {
					// margin-bottom: 0;
				}
			}

			.button-accent {
				margin-bottom: rem(17);
			}

			.field-text {
				p {
					color: #8E8E93;
				}
			}
		}
	}
}

.modal_overlay.active {
	display: flex;
}