.quality {
	margin-bottom: rem(280);

	height: rem(1280);

	&__top {
		margin-bottom: rem(130);
	}

	&__bottom {
		position: relative;

		height: rem(1495);

		img {
			position: absolute;
			top: rem(-400);
			left: 0;
			width: 100%;
			// height: 100%;
			height: rem(1495);
			display: block;
			// background-position: top center;
			object-fit: cover;
			object-position: top center;
		}

		&_wrap {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			display: flex;
			justify-content: center;

			.item {
				position: absolute;
				z-index: 10;
				background-color: rgba(40, 40, 46, .96);
				padding: rem(40) rem(50);

				transition: var(--transition);

				&:hover {
					z-index: 11;
					background-color: #34343D;

					.item__image {
						.line {
							opacity: 1;
						}
					}

					.item__descr {
						display: block;
					}
				}

				&__image {
					position: absolute;

					.line {
						opacity: 0;
					}
				}

				&__title {

					font-size: rem(24);
					line-height: 120%;
					color: white;
				}

				&__descr {
					font-size: rem(16);
					line-height: 140%;
					color: #8E8E93;
					margin-top: rem(15);
					display: none;
				}
			}

			.item.active {

				.item__image {
					.line {
						opacity: 1;
					}
				}

				.item__descr {
					display: block;
				}
			}
			.item_1 {
				top: rem(65);
				left: rem(10);

				width: rem(256);

				.item__image {
					top: rem(20);
					right: rem(-32);
				}
			}

			.item_2 {
				top: rem(142);
				right: rem(240);
				padding: rem(30) rem(50);

				width: rem(244);

				.item__image {
					top: rem(10);
					left: rem(-32);
				}
			}

			.item_3 {
				top: rem(382);
				left: rem(238);
				padding: rem(30) rem(50);

				width: rem(380);

				.item__image {
					top: rem(10);
					left: rem(-32);
				}
			}

			.item_4 {
				top: rem(520);
				left: rem(496);
				padding: rem(30) rem(50);

				width: rem(413);

				.item__image {
					top: rem(10);
					right: rem(-32);
				}
			}
		}

		.container {
			position: relative;
			width: 100%;
			// height: 100%;
		}

	}
}