.footer {

	&__copy {
		flex: 0 0 100%;
		max-width: 100%;

		p {
			padding-bottom: rem(52);
			padding-top: rem(80);
			font-size: rem(14);
			line-height: 140%;
			color: #B9B9B9;
		}
	}

	&__top {
		img {
			display: block;
			width: 100%;
			height: rem(650);
			object-fit: cover;
			margin-bottom: rem(80);
		}

		&_nav {
			margin-bottom: rem(80);
			ul {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;

				li {

					a {
						font-size: rem(12);
						line-height: 140%;
						letter-spacing: rem(1);
						color: var(--c-text-black);
						text-transform: uppercase;

						&:hover {
							color: var(--c-accent);
						}
					}
				}
			}
		}
	}

	&__bottom {

		.container {
			display: flex;
			flex-wrap: wrap;

			justify-content: space-between;
		}

		&_left {
			display: flex;
			flex-direction: column;

			.item {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				margin-bottom: rem(24);

				&:last-child {
					margin-bottom: 0;
				}

				&__icon {
					margin-right: rem(14);
				}

				&__text {
					font-size: rem(12);
					line-height: 120%;
					color: var(--c-text-black);
					max-width: rem(300);
				}
			}

			a.item {
				.item__text {
					font-size: rem(18);
					line-height: 140%;
					color: var(--c-text-black);

					
				}
			}
		}

		&_center {
			ul {
				li {
					margin-bottom: rem(24);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						font-size: rem(14);
						line-height: 140%;
						color: #8E8E93;
					}
				}
			}
		}

		&_right {
			display: flex;
			flex-direction: column;

			.button-accent {
				margin-bottom: rem(36);
			}

			.social {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				span {
					color: #B9B9B9;
					line-height: 140%;
					margin-right: rem(16);
				}

				a {
					margin-right: rem(12);

					&:hover {
						svg {
							.line {
								fill: var(--c-accent);
							}

							.line2 {
								fill: white;
								stroke: white;
							}
						}
					}

					svg {
						path {
							transition: var(--transition);
							stroke: var(--c-accent);
						}
					}

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}

	}
}