.header {
	position: relative;
	height: rem(920);

	background: url(../img/header_bg.jpg) no-repeat top center;
	background-size: cover;
	clip-path: polygon(0% 0%, 100% 0, 100% 91%, 49% 100%, 0 91%);

	margin-bottom: rem(150);


	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(180deg, #1E2139 3.83%, rgba(30, 33, 61, 0) 33.05%, rgba(196, 196, 196, 0) 67.94%);
		z-index: -1;
		opacity: .3;
	}

	> .container {
		display: flex;
		flex-direction: column;
	}

	&__top {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		padding-top: rem(45);
		padding-bottom: rem(75);

		&_nav {
			padding-bottom: rem(20);
			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(40);

					&:last-child {
						margin-right: 0;
					}

					a {
						font-size: rem(12);
						line-height: 140%;
						color: white;
						letter-spacing: rem(1.5);
						text-transform: uppercase;

						&:hover {
							opacity: .75;
						}
					}
				}
			}
		}

		&_contact {
			position: relative;
			display: flex;
			flex-direction: column;

			a {
				font-weight: 500;
				color: white;
			}

			button {
				font-size: rem(12);
				color: white;
				text-transform: lowercase;
				text-decoration: underline;
			}

			.bs-overlay {
				position: absolute;
				top: 0;
				margin-top: rem(-24);
				margin-left: rem(-32);
				left: 0;
				background: url(../img/bs-overlay-phone.svg) no-repeat;
				background-size: cover;

				width: rem(195);
				height: rem(97);

				z-index: -1;
			}
		}
	}

	&__bottom {
		display: flex;
		flex-direction: column;
		align-items: center;

		.logo {
			margin-bottom: rem(40);
		}

		.title {
			position: relative;
			font-size: rem(100);
			line-height: 125%;
			letter-spacing: rem(10);
			color: white;
			text-transform: uppercase;
			margin-bottom: rem(5);

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: rem(972);
				margin-left: rem(-170);
				height: rem(252);
				margin-top: rem(-42);
				filter: blur(50px);
				background: url(../img/bs-overlay-title.svg) no-repeat;
				z-index: -1;
			}
		}

		.subtitle {
			font-size: rem(18);
			line-height: 140%;
			color: white;
			text-transform: uppercase;
			letter-spacing: rem(2);

			margin-bottom: rem(80);

		}

		.button-play {
			.line2 {
				display: none;
			}

			&:hover {
				.line {
					display: none;
				}
				.line2 {
					display: block;
				}
			}
		}


	}

}