.form_present {
	position: relative;
	width: 100%;
	// overflow: hidden;

	height: rem(543);
	background: url(../img/section5/message_bg.png) no-repeat top center;
	background-size: cover;

	margin-bottom: rem(248);

	&::before {
		content: "";
		position: absolute;
		bottom: rem(-374);
		right: 0;
		width: rem(380);
		height: rem(743);
		background: url(../img/section5/right_bg.png) no-repeat;
	}

	>.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
	}

	&__left {
		margin-left: rem(100);
	}

	&__right {
		position: absolute;
		top: rem(35);
		right: rem(32);

		img {
			
		}
	}
}