.gallery {
	margin-bottom: rem(64);

	&__top {
		margin-bottom: rem(50);

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.sect-tab-button {
				margin-left: rem(140);
			}

			.button-play {
				margin-left: auto;

				display: flex;
				align-items: center;
				

				&:hover {
					.text {
						color: var(--c-accent);
					}

					.icon {
						background-color: var(--c-accent);
					}
				}

				.text {
					font-size: rem(12);
					text-transform: uppercase;
					color: var(--c-text-black);

					transition: var(--transition);
				}

				.icon {
					margin-left: rem(27);
					background-color: var(--c-text-black);
					width: rem(52);
					height: rem(52);
					border-radius: 50%;

					display: flex;
					align-items: center;
					justify-content: center;

					transition: var(--transition);
				}
			}
		}
	}

	&__bottom {
		max-height: rem(800);
		position: relative;

		.swiper {
			height: rem(800);
			.swiper-slide {
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		.swiper-nav-wrap {
			position: absolute;
			top: 50%;
			left: 0;
			margin-top: rem(-50);
			width: 100%;

			display: flex;
			justify-content: center;

			.container {
				position: relative;
				// z-index: 10;
				width: 100%;
				height: rem(100);

				display: flex;
				align-items: center;
			}
		}

		.swiper-nav {
			// position: absolute;
			// top: 50%;
			// z-index: 10;
			// left: 0;

			display: flex;
			justify-content: space-between;
			width: 100%;

			.swiper-button-disabled {
				opacity: 0;
			}

			button {
				position: relative;
				z-index: 10;
				width: rem(100);
				height: rem(100);
				border-radius: 50%;
				background-color: rgba(255,255,255,.3);

				&:hover {
					background-color: rgba(255,255,255,.45);
				}
			}

			&-next {
				svg {
					position: absolute;
					top: 50%;
					margin-top: rem(-10);
					left: rem(-17);
				}
			}

			&-prev {
				svg {
					position: absolute;
					top: 50%;
					margin-top: rem(-10);
					right: rem(-17);

					transform: rotate(-180deg);
				}
			}
		}
	}

	.gallery_tab_wrap {
		display: none;
	}

	.gallery_tab_wrap.active {
		display: block;
	}
}