.investment {
	margin-bottom: rem(228);

	&__top {
		margin-bottom: rem(50);

		.sect-title-40 {
			margin-bottom: rem(48);
		}

		.sect-text {
			margin-bottom: rem(28);

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;

		&_left {
			display: flex;
			flex-direction: column;
			flex: 0 0 rem(672);
			max-width: rem(672);
			margin-right: rem(80);

			margin-top: rem(24);

			.item {
				display: flex;
				// flex-wrap: wrap;
				padding-top: rem(18);
				padding-bottom: rem(72);

				border-top: rem(1) solid #EEEEFA;

				&__left {
					flex: 0 0 rem(296);
					max-width: rem(296);

					padding-top: rem(10);

					font-size: rem(12);
					color: #8E8E93;
					line-height: 140%;
					letter-spacing: rem(1);

					text-transform: uppercase;
				}

				&__right {
					text-transform: none;
				}
			}
		}

		&_right {
			flex: 0 0 rem(585);
			max-width: rem(585);

			margin-right: rem(-200);

			img {
				width: 100%;
				height: rem(355);
				object-fit: cover;
				display: block;
			}
		}
	}
}