.info {
	position: relative;
	margin-bottom: rem(270);

	&::before {
		content: "";
		position: absolute;
		bottom: rem(-342);
		left: 0;
		background: url(../img/section2/left_bg.png) no-repeat top left;
		background-size: cover;
		width: rem(285);
		height: rem(757);
		z-index: -1;

		@include min1440 {

			left: rem(-120);
		// 	width: 100%;
		// 	height: rem(320);
		}
	}

	> .container {
		display: flex;
		flex-wrap: wrap;
		position: relative;
	}

	&__left {
		display: flex;
		flex-direction: column;

		flex: 0 0 rem(410);
		max-width: rem(410);
		margin-right: rem(112);

		.subtitle {
			color: #878787;
			font-size: rem(12);
			letter-spacing: rem(2);
			line-height: 140%;
			text-transform: uppercase;
			margin-bottom: rem(44);
		}

		.sect-title-40 {
			margin-bottom: rem(40);
		}

		.sect-text {
			margin-bottom: rem(40);

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&__right {
		flex: 0 0 rem(870);
		max-width: rem(870);

		margin-right: rem(-200);
	}

	&__items {
		position: absolute;
		bottom: rem(-100);
		right: rem(194);

		padding: rem(40) rem(90);

		display: flex;
		flex-wrap: wrap;
		background-color: white;
		box-shadow: 0px 22px 39px -26px rgba(199, 211, 217, 0.5);

		.item {
			display: flex;
			flex-direction: column;

			margin-right: rem(72);

			&:last-child {
				margin-right: 0;
			}

			span {
				&:last-child {
					line-height: 140%;
					color: #B9B9B9;
					text-transform: lowercase;
				}
			}
		}
	}
}