.tab_info {
	position: relative;
	height: rem(715);
	background-repeat: no-repeat;
	background-position: top center;
	background-size: cover;

	margin-bottom: rem(270);

	transition: var(--transition);

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(40, 40, 46, .8);
	}

	> .container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		position: relative;
		z-index: 1;
	}

	&__item {
		display: none;
		flex-direction: column;

		padding: rem(100);

		box-shadow: 0px 22px 39px -26px rgba(199, 211, 217, 0.5);

		.image {
			position: relative;
			width: rem(144);
			height: rem(144);
			border-radius: 50%;
			background-color: #F7F6F3;
			margin-bottom: rem(60);
			margin-top: rem(46);

			svg {
				position: absolute;
				top: rem(-28);
				right: rem(-14);
			}
		}

		.sect-title-28 {
			margin-bottom: rem(16);
			max-width: rem(300);
		}

		.sect-text {
			margin-bottom: rem(60);
		}

		.button-accent {
			align-self: flex-start;
		}
	}

	&__tab2 {
		.image {
			svg {
				top: rem(-4);
				right: rem(-35);
			}
		}
	}

	&__tab4 {
		.image {
			svg {
				top: rem(-4);
				right: rem(-35);
			}
		}
	}

	&__tab5 {
		.image {
			svg {
				top: rem(28);
				right: rem(-5.5);
			}
		}
	}

	&__item.active {
		display: flex;
	}

	&__left {
		background-color: white;
		width: rem(630);
		margin-top: rem(80);

		display: flex;
		flex-direction: column;


	}

	&__right {
		margin-left: rem(140);
		position: absolute;
		top: rem(190);
		right: rem(128);
		width: rem(310);
		height: rem(368);

		ul {
			display: flex;
			flex-direction: column;

			li {
				margin-bottom: rem(48);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-size: rem(18);
					line-height: 140%;
					color: white;
					text-transform: uppercase;

					display: flex;
					align-items: center;

					white-space: nowrap;

					svg {
						width: 0;
						opacity: 0;
						margin-right: 0;
						transition: var(--transition);
					}

					&:hover {
						color: var(--c-accent);

						svg {
							width: rem(24);
							opacity: 1;
							margin-right: rem(28);
						}
					}
				}

				a.active {
					color: var(--c-accent);

					svg {
						flex: 0 0 rem(24);
						width: rem(24);
						opacity: 1;
						margin-right: rem(28);
					}
				}
			}
		}
	}
}