.form_message {
	background-color: var(--c-text-black);
	padding: rem(70) rem(65);

	width: rem(506);

	.sect-title-24 {
		color: white;
		margin-bottom: rem(16);
	}

	.sect-text {
		margin-bottom: rem(74);
	}

	.field-set {
		margin-bottom: rem(28);
	}

	.field-text {
		margin-bottom: rem(48);
	}

	.button-accent {
		width: 100%;
	}
	
}

.field-set {
	position: relative;
	

	.icon {
		position: absolute;
		bottom: rem(14);
		left: 0;
	}

	.error-message {
		position: absolute;
		bottom: rem(-24);
		left: 0;
		color: #9D4A4A;
	}

	.field {
		padding: rem(16) rem(10) rem(16) rem(45);

		border-bottom: rem(1) solid rgba(255,255,255,.2);
		background-color: transparent;
		width: 100%;
		color: white;
		font-size: rem(16);

		@include placeholder {
			color: #7D7D80;
		}
	}
}
.field-set.error {
	.field {
		border-color: #9D4A4A !important;
	}
}

.field-text {
	p {
		color: rgba(185,185,185,.5);
		font-size: rem(12);
		line-height: 140%;

		a {
			text-decoration: underline;
		}
	}
}