.layouts {
	position: relative;
	margin-bottom: rem(250);

	&::before, &::after {
		content: "";
		position: absolute;
		z-index: -1;
		top: rem(-120);
	}

	&::before {
		left: 0;
		width: rem(306);
		height: rem(744);
		background: url(../img/section6/left_bg.png) no-repeat;
	}

	&::after {
		right: 0;
		width: rem(547);
		height: rem(744);
		background: url(../img/section6/right_bg.png) no-repeat;
	}

	&__top {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		margin-bottom: rem(52);

		.sect-title-40 {
			margin-right: rem(120);
		}

		
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;

		&_left {
			flex: 0 0 rem(700);
			max-width: rem(700);

			.layouts_tab_wrap {
				display: none;
			}
			.layouts_tab_wrap.active {
				display: block;
			}
		}

		&_right {
			margin-left: rem(100);

			flex: 0 0 rem(310);
			max-width: rem(310);

			.title {
				font-size: rem(36);
				line-height: 140%;
				color: var(--c-text-black);
				margin-bottom: rem(24);

				span {
					color: var(--c-accent);
				}
			}

			.sect-text {
				margin-bottom: rem(24);

				&:last-of-type {
					margin-bottom: 0;
				}
			}

			.items {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				margin-top: rem(64);
				margin-bottom: rem(74);

				.item {
					display: flex;
					// flex-wrap: wrap;
					align-items: center;
					width: calc((100% - 75px) / 2);

					margin-bottom: rem(46);

					&:nth-last-child(2), &:last-child {
						margin-bottom: 0;
					}

					&__image {
						flex: 0 0 rem(23);
						max-width: rem(23);
						margin-right: rem(22);
						display: flex;
						justify-content: center;
					}

					span {
						line-height: 140%;
						color: var(--c-text-black);
						white-space: nowrap;

						b {
							font-size: rem(24);
							margin-right: rem(3);
							font-weight: normal;
						}
					}
				}
			}
		}
	}
}