.map {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(230);

	&__top {
		margin-bottom: rem(60);

		.container {
			display: flex;
			flex-wrap: wrap;
			padding: 0 rem(80);
		}

		.item {
			cursor: pointer;
			display: flex;
			flex-direction: column;

			flex: 1;

			padding: 0 rem(50);

			border-right: rem(1) solid rgba(86, 86, 92, .2);

			&:last-child {
				border-right: 0;
			}

			&:hover {
				.item__top {
					color: var(--c-accent);
				}

				.item__bottom {
					color: var(--c-text-black);
				}
			}

			&__top {
				line-height: 140%;
				color: var(--c-text-black);
				font-size: rem(24);
				margin-bottom: rem(4);

				transition: var(--transition);

				span {
					font-size: rem(40);
				}
			}

			&__bottom {
				font-size: rem(14);
				line-height: 120%;
				color: #8E8E93;
				transition: var(--transition);
			}
		}
	}

	&__bottom {
		position: relative;

		height: rem(670);

		img {
			width: 100%;
			height: 100%;
			display: block;
			object-fit: cover;
		}

		&_wrap {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			display: flex;
			justify-content: center;
		}

		.container {
			position: relative;
			width: 100%;
			// height: 100%;
		}

		.item {
			position: absolute;
			z-index: 10;
			box-shadow: 0px 22px 39px -26px rgba(199, 211, 217, 0.5);
			background-color: white;
			padding: rem(40) rem(50);
			border: rem(1) solid white;

			transition: var(--transition);

			&:hover {
				border-color: rgba(168,144,109,.15);
			}

			&__image {
				position: absolute;

				.line {
					opacity: 0;
				}
			}

			&__title {
				
				font-size: rem(18);
				line-height: 120%;
				color: var(--c-text-black);
			}

			&__descr {
				font-size: rem(14);
				line-height: 120%;
				color: #8E8E93;
				margin-top: rem(15);
				display: none;
			}
		}

		.item.active {

			.item__image {
				.line {
					opacity: 1;
				}
			}

			.item__descr {
				display: block;
			}
		}

		.item_1 {
			top: rem(470);
			left: rem(184);

			width: rem(250);

			.item__image {
				top: rem(20);
				right: rem(-32);
			}
		}

		.item_2 {
			top: rem(38);
			right: rem(82);
			padding: rem(30) rem(50);

			width: rem(294);
			.item__image {
				top: rem(5);
				left: rem(-32);
			}
		}

		.item_3 {
			top: rem(317);
			left: rem(538);
			padding: rem(30) rem(50);

			width: rem(186);

			.item__image {
				top: rem(5);
				left: rem(-32);
			}
		}

		.item_4 {
			top: rem(554);
			left: rem(496);
			padding: rem(30) rem(50);

			width: rem(256);

			.item__image {
				top: rem(5);
				left: rem(-32);
			}
		}
	}
}